*{
    padding: 0;
    margin: 0;
}
body{
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #334;
    font-family: cursive;
}
.input-text{
    width: 360px;
    outline: none;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    padding: 10px;
    margin-top: 170px;
}
.todo-block{
    max-width: 370px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(226, 125, 10);
    margin: 5px auto 0;
    border-radius: 5px;
    list-style-type: none;
}
.todo-content{
    font-size: 22px;
    color: rgb(255, 255, 255);
}
.check{
    margin-left: 10px;
}
.todo-delete{
    border: none;
    font-size: 24px;
    font-weight: 600;
    padding: 10px;
    color: #444242;
    border-radius: 5px;
}
.todo-lang{
    width: 350px;
    position: absolute;
    top: 20px;
}
.todo-select{
    padding: 5px 20px;
    font-size: 18px;
    outline: none;
    border-radius: 5px;
}
.todo-description{
    color: #ccc;
}